class KukunModal {
    constructor(modalBackgroundWrapper, parameters) {
        this.backgroundWrapper = modalBackgroundWrapper;
        this.modalWrapper = this.backgroundWrapper.children[0];
        this.btnCloseModal = this.backgroundWrapper.querySelector('.k-modal-close');
        this.modalParameters = parameters;
        this._addParameters();
        this._loadEvents();
    }

    _loadEvents() {
        this.backgroundWrapper.addEventListener('click', (e) => {
            const target = e.target;
            if (!this.modalWrapper.contains(target)) {
                this.close();
            }
        })

        if (this.btnCloseModal) {
            this.btnCloseModal.addEventListener('click', (e) => {
                this.close();
            })
        }
    }
    _addParameters() {
        if(this.modalParameters.elmntContainer) {
            this.modalWrapper.classList.add(`k-width-${this.modalParameters.elmntContainer}`)
        }
        else {
            this.modalWrapper.classList.add(`k-width-md-40`)
        }
        this.modalWrapper.style.maxWidth = `${this.modalParameters.maxElmntWidth}px` || '600px'
        this.modalWrapper.style.overflowY = this.modalParameters.scrollable ? "auto" : "hidden"
    }

    open() {
        this.backgroundWrapper.style.display = 'flex';
        setTimeout(() => {
            this.modalWrapper.classList.add('k-displayed-modal');
        }, 100);
    }

    close() {
        this.backgroundWrapper.style.display = 'none';
        this.modalWrapper.classList.remove('k-displayed-modal');
    }

    toggle() {
        if(this.modalWrapper.classList.contains("k-displayed-modal")) {
            this.backgroundWrapper.style.display = 'none';
            this.modalWrapper.classList.remove('k-displayed-modal');
        }
        else {
            this.backgroundWrapper.style.display = 'flex';
            setTimeout(() => { 
                this.modalWrapper.classList.add("k-displayed-modal");
            }, 100)
        }
    }

    remove() {
        this.backgroundWrapper.style.display = 'none';
        this.backgroundWrapper.style.visibility = 'hidden';
        this.modalWrapper.style.display = 'none';
    }
}

let modalInstances = [];

const init = (background_rappers, modalParameters) => {
    if (background_rappers) {
        if (typeof background_rappers.length == 'undefined') {
            const htmlElement = background_rappers;
            const parameters = modalParameters;
            modalInstances = new KukunModal(htmlElement, parameters);
        } else {
            for (let i = 0; i < background_rappers.length; i++) {
                const htmlElement = background_rappers[i];
                let modalInstance = new KukunModal(htmlElement);
                modalInstances.push(modalInstance);
            }
        }
    }

    return modalInstances;
}


export {
    init
};